@import '_vars.scss';

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote {
  &:before, &:after {
    content: '';
    content: none; } }

q {
  &:before, &:after {
    content: '';
    content: none; } }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html,body{
  font: 14px/1 $font;
  *{
    box-sizing: border-box;
  }
}

.flex{
  display: flex;
}

.align_c{
  align-items: center;
}

.margin_a{
  margin: 0 auto;
}

.Rain,.Clouds,.Clear,.ClearNight,.Snow{
  width: 100px;
  height: 100px;
  background-size: contain;
}

.Rain{
  background: url(../img/weather-state-icons/rain.svg) no-repeat 50% 50%;
}

.Clouds{
  background: url(../img/weather-state-icons/clouds.svg) no-repeat 50% 50%;
}

.night.Clouds{
  background: url(../img/weather-state-icons/night-clouds.svg) no-repeat 50% 50%;
}

.Snow{
  background: url(../img/weather-state-icons/snow.svg) no-repeat 50% 50%;
}

.Clear{
  background: url(../img/weather-state-icons/clear.svg) no-repeat 50% 50%;
}

.night.Clear{
  background: url(../img/weather-state-icons/night-clear.svg) no-repeat 50% 50%;
}